import React, { useRef } from 'react'
import { useState } from 'react';
import Button from 'react-bootstrap/Button';
import Offcanvas from 'react-bootstrap/Offcanvas';
import DatePicker from "react-datepicker";
import "react-datepicker/dist/react-datepicker.css";


const EditCard = ({ data, setData, goal }) => {

    const [startDate, setStartDate] = useState(goal.date);

    const [show, setShow] = useState(false);

    const handleClose = () => setShow(false);
    const handleShow = () => setShow(true);


    const titleRef = useRef(null)
    const bodyRef = useRef(null)


    const markCompleted = () => {
        const newArray = [];
        data.map(card => {
            if (card.key !== goal.key) {
                newArray.push(card);
            }
        })

        setData([...newArray])
    }


    const buttonClickHandler = () => {
        const newArray = [];
        data.map(card => {
            if (card.key !== goal.key) {
                newArray.push(card);
            }
        })

        const title = titleRef.current.value
        const body = bodyRef.current.value
        setData([{ key: goal.key, title: title, body: body, date: startDate }, ...newArray])
    }

    return (
        <>
            <Button variant="primary m-2" onClick={handleShow}>
                Edit task
            </Button>
            <Button variant="warning m-2" onClick={markCompleted}>
                Completed
            </Button>
            <Offcanvas className="bg-dark text-white" show={show} onHide={handleClose}>
                <Offcanvas.Header closeButton>
                    <Offcanvas.Title>Edit Task</Offcanvas.Title>
                </Offcanvas.Header>
                <Offcanvas.Body>
                    <div className='p-3'>
                        <p className='lead'>Title:</p>
                        <input ref={titleRef} type='text' className='form-control' defaultValue={goal.title} />
                    </div>
                    <div className='p-3'>
                        <p className='lead'>Body:</p>
                        <textarea ref={bodyRef} type='text' rows={5} className='form-control' defaultValue={goal.body} />
                    </div>
                    <div className='p-3'>
                        <p className='lead'>Date:</p>
                        <DatePicker selected={startDate} onChange={(date) => setStartDate(date)} />
                        {/* <input type='text' className='form-control' defaultValue={goal.date} /> */}
                    </div>
                    <div className='p-3'>
                        <button className='btn btn-primary' onClick={buttonClickHandler}>Update task</button>
                    </div>
                </Offcanvas.Body>
            </Offcanvas>
        </>
    )
}

export default EditCard
